            |remotePath|String|\`test/getPage\`;\`remotePath\`,\`http://localhost:8081/test4/getPage\`||||
            |remoteParam|Object| table|||{}|
            |remoteMethodType|String|\`get\` \`post\` \`delete\` \`post\` \`put\` \`patch\` \`head\`|||get|
            |remoteTimeout|Number|,\`this.$ld.requestSetting.config.timeout = 2000;\`|2000ms||-|`
          },
          {
            h2: '`el-table-property`',
            md: `|||||||
            |-|-|-|-|-|-|
            |border|Boolean||||true|
            |stripe|Boolean| table|||true|
            |indent|Number||||20|
            |tooltipEffect|String|tooltip effect |dark/light||-|
            |fit|Boolean||||true|
            |highlightCurrentRow|Boolean||||true|
            |defaultExpandAll|Boolean| Table |||false|
            |defaultSort|Object| prop proporder|\`order\`: ascending, descending||prop, order, ascending|
            |showSummary|Boolean||||false|
            |sumText|String|||||
            |summaryMethod|Function({ columns, data })|||||
            |spanMethod|Function({ row, column, rowIndex, columnIndex })|	||||`
          },
          {
            h2: '`format`',
            javascript: `
            //
            layout:[
            	{
            		prop:'sex',
            		label:'',
            		format:(val)=>{
            			return val==1?'':''
            		}
            	}
            ]

            //
            layout:[
            	{
            		prop:'birthday',
            		label:'',
            		format:(val)=>{
            			return !val?'':this.$ld.util.getDate(0,new Date(val)); //https://github.com/dongmingzhixiu/layout-dynamic-ui/blob/main/doc/ld-util.md#getDate
            		}
            	}
            ]
            `.replace(/^          /gm, ""),
          },
          {
            h2: '`html`',
            javascript: `
            //
            layout:[
            	{
            		prop:'sex',
            		label:'',
            		html:(val)=>{
            			return \`<div style="\${val==1?'color:red':'color:green'}">\${val==1?'':''}</div>\`;
            		}
            	}
            ]

            // 
            layout:[
            	{
            		prop:'birthday',
            		label:'',
            		html:(val)=>{
            			return \`<img src="\${val}"/>\` //
            		}
            	}
            ]
            `.replace(/^          /gm, ""),
          },
          {
            h2: '`replace`',
            tip_p: `<br/>
            \`\`\`\`<br/>
            <span class="c8">()</span>`,
            md: `||||||
            |-|-|-|-|-|
            |remotePath|String|||:\`test/getUserById\`,|
            |remoteMethodType|String|||\`get\` \`post\` \`delete\` \`post\` \`put\` \`patch\` \`head\`|
            |label|String|||'createdName'|
            |id|String|||'id'|
`
          },
          {
            h1: '.',
            md: `|slot name||||
            |-|-|-|-|
            |expand|||-|
            |tools|||-|
            |toolsHeader|||-|
            |append||<br/> slot slot ||
            `
          }

        ]
      }
    },
    methods: {
      getCheckBox(e) {
        this.enabledCheckList = e;
      },
      /**
       * 
       * @param {Object} data
       */
      getTreeDataMethod(_data) {
        //
        // let data =_data['data']['list']
        //
        let data = _data['data'];

        function getChildren(data, pid) {
          let d = data.filter(item => item.parentId == pid) || [];
          if (d.length > 0) {
            d.map(_item => {
              let _pid = _item['id'];
              let _d = getChildren(data, _pid);
              _item['children'] = _d;
              _item['children'].map(_citem => {
                let _cpid = _citem['id'];
                let _cd = getChildren(data, _cpid);
                _citem['children'] = _cd;
              })
            });
          }
          return d;
        }
        let d = data.filter(item => !item.parentId || item.parentId == '0');
        if (d.length > 0) {
          d=getChildren(data,'0')
        }
        //
        //  _data['data']['list']=d;
        //
        _data['data'] = d;
        return _data;
      }
    }
  }
</script>

