<template>
  <!--  -->
  <div class="box-b p10 h">
    <el-card class="doc w h  over-a-y">
      <ld-doc :doc="doc" class="w h">
        <template v-slot:foot="e">
          <foot></foot>
        </template>
      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: 'installs',
    data() {
      return {
        doc: [{
            H1: '',
            tip: '',
          }, {
            h1: '',
            h2: 'npm',
            p: ' npm  webpack ',
            bash: 'npm i layout-dynamic-ui',
            // 
            slot: 'custom',
          },
          {
            p: 'main.js',
            javascript: `
                import Vue from 'vue';
                import App from './App';
                import ElementUI from 'element-ui';
                import 'element-ui/lib/theme-chalk/index.css';

                import layoutDynamicUI from 'layout-dynamic-ui';
                import 'layout-dynamic-ui/lib/index.css';
                Vue.use(ElementUI);
                Vue.use(layoutDynamicUI);
                Vue.config.productionTip = false
                new Vue({
                  el: '#app',
                  components: { App },
                  template: '<App/>'
                })`.replace(/^             /gm, ' '),
            tip_s: ''
          },
          {
            slot: 'foot'
          }
        ]
      }
    }
  }
</script>

<style>
</style>



