export default {
  layout: [{
      prop: 'mode',
      label: '',
      type: 'select',
      options: [{
        label: '',
        value: 'vertical'
      }, {
        label: '',
        value: 'horizontal'
      }],
			change:(val)=>{
				debugger
				console.log(val);
				return {
					collapse:{
						visabled:val=='vertical',
					},
					uniqueOpened:{
						visabled:val=='vertical',
					},
				}
			}
    },
    {
      prop: 'collapse',
      label: '',
      type: 'select',
      options: [{
        label: '',
        value: true
      }, {
        label: '',
        value: false
      }]
    },
    {
      prop: 'backgroundColor',
      label: '',
      type:'color',
      value: '#ffffff'
    },
    {
      prop: 'textColor',
      label: '',
      type:'color',
      value: '#303133'
    },
    {
      prop: 'activeTextColor',
      label: '',
      type:'color',
      value: '#409EFF'
    },
    {
      prop: 'uniqueOpened',
      label: '',
      type: 'select', 
      options: [{
        label: '',
        value: true
      }, {
        label: '',
        value: false
      }]
    }
  ]
}



