<template>
	<!--  -->
	<div class="box-b p10 h loading">
		<el-card class="doc w h  over-a-y">
			<ld-doc :doc="doc">
				<template v-slot:frist="e">
					<el-card class="p10 b-i1">
						<ld-menu-tree class="w-200" menu-trigger="click" style="height: 500px;overflow: hidden auto;"
							:tree="tree">
						</ld-menu-tree>
					</el-card>
				</template>

				<template v-slot:secound="e">
					<ld-forms class="m-t10 m-b10" :form="forms" :layout="layout" :is-row="true">
						<template v-slot:buttons="e">
							<div style="position: relative;">
								<el-button type="primary m-l10" @click="copy('#doc2',true)"></el-button>
							</div>
						</template>
					</ld-forms>
					<el-card class="p10 b-i1">
						<ld-menu-tree :mode="forms['mode']"
							:class="{'w-200 ':forms['mode']== 'vertical'&&forms['collapse']== false,'w':forms['mode']!= 'vertical','w-60':forms['mode']== 'vertical'&&forms['collapse']== true}"
							:collapse="forms['collapse']||false" :background-color="forms['backgroundColor']"
							:text-color="forms['textColor']" :active-text-color="forms['activeTextColor']"
							:unique-opened="forms['uniqueOpened']" menu-trigger="click" style="overflow: hidden auto;"
							:style="{'height':forms['mode']== 'vertical'?'500px':'300px'}" :tree="tree">
						</ld-menu-tree>
					</el-card>
				</template>


				<template v-slot:foot="e">
					<foot></foot>
				</template>
			</ld-doc>

		</el-card>
	</div>
</template>

<script>
	import res from './menu-tree-res.js'
	export default {
		name: 'loading',
		data() {
			return {
				forms: {
					mode: 'vertical',
					collapse: false
				},
				layout: res.layout,

				tree: [{
						label: '1',
						prop: 'menu1',
						icon: 'el-icon-platform-eleme',
						children: [{
								label: '1',
								prop: 'cmenu1',
								children: [{
										label: '1',
										prop: 'ccmenu1',
									},
									{
										label: '2',
										prop: 'ccmenu2',
                    icon: 'el-icon-platform-eleme',
										children: [{
											label: '1',
											prop: 'cccmenu1',
										}, {
											label: '1',
											prop: 'cccmenu2',
										}]
									},
								]
							},
							{
								label: '2',
								prop: 'cmenu2'
							},
							{
								label: '3',
								prop: 'cmenu3',
							},
						]
					},
					{
						label: '2',
						prop: 'menu2',
						icon: 'el-icon-eleme',
						children: [{
								label: '2',
								prop: 'ccmenu2',
							},
							{
								label: '3',
								prop: 'ccmenu3',
								children: [{
									label: '4',
									prop: 'cccmenu4',
								}, {
									label: '5',
									prop: 'cccmenu5',
								}]
							},
						]
					},
					{
						label: '3',
						prop: 'menu3',
						icon: 'el-icon-s-tools',
					},
					{
						label: '4',
						prop: 'menu4',
						icon: 'el-icon-s-goods'
					},
					{
						label: '5',
						prop: 'menu5',
						icon: 'el-icon-success'
					},
				],

				doc: [{
						title: '`ld-menu-tree`',
						tip: '',
						tip_d: 'children!',
					},
					{
						h1: '.',
						h2: '',
						html: `
            <ld-menu-tree class="w-200" style="height: 500px;overflow: hidden auto;" :tree="tree"></ld-menu-tree>
            `.replace(/^        /gm, ''),
						javascript: `
            export default {
                data() {
                  return {
                    tree:[
                      {label: '1',prop: 'menu1',
                        children: [
                          {label: '1',prop: 'cmenu1',
                            children: [
                              {label: '1',prop: 'ccmenu1',},
                              {label: '2',prop: 'ccmenu2',
                                children: [
                                    {label: '1',prop: 'cccmenu1'},
                                    {label: '1',prop: 'cccmenu2'}]
                                },
                            ]},
                          {label: '2',prop: 'cmenu2'},
                          {label: '3',prop: 'cmenu3'}]
                      },
                      {label: '2',prop: 'menu2'},
                      {label: '3',prop: 'menu3'},
                      {label: '4',prop: 'menu4'},
                      {label: '5',prop: 'menu5'},
                  ],
                }
              }
            }`.replace(/^          /gm, ''),
						p: '',
						slot: 'frist',
					},
					{
						h1: '.',
						slot: 'secound',
						html: `
						<ld-menu-tree :mode="forms['mode']"
							:class="{'w-200 ':forms['mode']== 'vertical'&&forms['collapse']== false,'w':forms['mode']!= 'vertical','w-60':forms['mode']== 'vertical'&&forms['collapse']== true}"
							:collapse="forms['collapse']||false" :background-color="forms['backgroundColor']"
							:text-color="forms['textColor']" :active-text-color="forms['activeTextColor']"
							:unique-opened="forms['uniqueOpened']" menu-trigger="click" style="overflow: hidden auto;"
							:style="{'height':forms['mode']== 'vertical'?'500px':'300px'}" :tree="tree">
						</ld-menu-tree>`.replace(/^						/gm, ""),
						javascript: `
							export default {
								data() {
									return {
										tree:[
											{label: '1',prop: 'menu1',icon: 'el-icon-platform-eleme',
												children: [{label: '1',prop: 'cmenu1',
													children:
														[{label: '1',prop: 'ccmenu1',},
														{label: '2',prop: 'ccmenu2',
														children:
															[{label: '1',prop: 'cccmenu1',},
															{label: '1',prop: 'cccmenu2',}]
														},
													]
												},
												{label: '2',prop: 'cmenu2'},
												{label: '3',prop: 'cmenu3',},
												]
											},
											{label: '2',prop: 'menu2',icon: 'el-icon-eleme',
												children:
													[{label: '2',prop: 'ccmenu2',},
													{label: '3',prop: 'ccmenu3',
														children:
															[{label: '4',prop: 'cccmenu4',},
															{label: '5',prop: 'cccmenu5',}]
														},
													]
											},
											{label: '3',prop: 'menu3',icon: 'el-icon-s-tools',},
											{label: '4',prop: 'menu4',icon: 'el-icon-s-goods'},
											{label: '5',prop: 'menu5',icon: 'el-icon-success'},
										]
									}
								}
							}`.replace(/^							/gm, ''),
					},
					{
						H1: '.',
						h2: '',
						md: `|||||||
								|-|-|-|-|-|-|
								|tree|Array||[]||[{label:'1'},{label:'2'},{label:'3'}][,tree](###tree)|
								|mode|String|(\`vertical\`\\|\`horizontal\`)|vertical|||
								|collapse|Boolean||false|| mode  vertical |
								|background-color|String||'#ffffff'|| hex |
								|text-color|String||'#303133'|| hex |
								|active-text-color|String||'#409EFF'|| hex |
								|collapse-transition|Boolean||false|||
								|menu-trigger|String|(\`click\`\\|\`hover\`)|click||( mode  horizontal )|
								|uniqueOpened|Boolean||false|||
								|defaultActive|String| index|'0'|||`
					},
					{
						h2: 'tree',
						md: `|||||||
								|-|-|-|-|-|-|
								|label|String||-||-|
								|icon|String||-||'el-icon-search';icon,|
								|disabled|Boolean||-||\`[]\`|
								|children|Array[Object]||-||[]\`{label:'1',children:[{label:'2'},{label:'3'},....]}\`|
`
					},
					{
						h2: 'Event',
						md: `||||||
						|-|-|-|-|-|
						|click|Function($event)|Object|||`
					},
					{
						h1: '.',
						tip_d: '`mode="horizontal"`',
						css: `
						.el-menu--horizontal.el-menu>.el-submenu {
							position: relative;
						}

						.el-menu--horizontal.el-menu>.el-submenu>.el-menu--horizontal {
							position: absolute;
						}

						`.replace(/^						/gm, ''),
					},
					{
						slot: 'foot'
					}
				]
			}
		},
		created() {}
	}
</script>

<style>
	.loading .el-card__body {
		padding: 0 !important;
	}

	.el-menu--horizontal.el-menu>.el-submenu {
		position: relative;
	}

	/* .el-menu--horizontal.el-menu>.el-submenu>.el-submenu__title {} */

	.el-menu--horizontal.el-menu>.el-submenu>.el-menu--horizontal {
		position: absolute;
	}
</style>



