<template>
  <div class="box-b p10 h ld-doc">
    <el-card class="doc w h">
      <!--  -->
      <ld-doc :doc="doc" class="w h">

      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        doc:[
          {
            title:'',
            tip:'...<a target="_black" href="https://github.com/dongmingzhixiu/layout-dynamic-ui/blob/main/doc/ld-frame.md">gitHubld-frame</a>',
            md:`# \`ld-frame\`

            ## 

            ![](https://github.com/dongmingzhixiu/layout-dynamic-ui/raw/main/effect/ld-frame.png)

            ## \`ld-frame\` 

            > \`ld-page-tabs\` 

            |||||||
            |-|-|-|-|-|-|
            |menuTreeUniqueOpened|Boolean|||||
            |menuTree|Array||||[](./ld-menu-tree.md##\`ld-menu-tree\`)|
            |leftHeadInfo|Object||||{image:'logo',label:'',text:''}||
            |passEventUp|Boolean||true||[](##)|
            |showConfirm|Boolean||true|||


            ## Event 
             > 


            ## Slot 
            |||||
            |-|-|-|-|
            |headCenter||||
            |logo||||
            |page||item {} tab||


            ## 

            > 

            -  \`passEventUp = true\` 

            -- 
            \`\`\`javascript
            	//eventeventMethodeventParam
            	this.$emit('events',{eventMethod:'click',eventParam:{}});
            \`\`\`
            \`ld-page-tabs\`
            \`\`\`html
            <ld-page-tabs @events="getEvent"></ld-page-tabs>
            \`\`\`
            \`\`\`javascript
            //....
            	getEvent(event){
            		// 
            	}
            //...
            \`\`\`

            -  \`passEventUp = false\` 
            -- 
            \`\`\`javascript
            	//eventeventMethodeventParam
            	this.$emit('events',{eventMethod:'click',eventParam:{}});
            \`\`\`
            \`ld-page-tabs\`
            \`\`\`html
            <ld-page-tabs @click="getEvent"></ld-page-tabs>
            \`\`\`
            \`\`\`javascript
            //....
            	getEvent(event){
            		// eventeventParam
            		// 
            	}
            //...
            \`\`\`

            # 

            ## ,, logo 

            ### \`home.vu\`

            \`\`\`html
            <!--home.vue-->
            <ld-frame ref="frame" class="h-vh w" :menu-tree="menuTree" :left-head-info="leftHeadInfo" @openTabs="openTabs" @closeTabs="closeTabs" @refreshTabs="refreshTabs" :pass-event-up="false">
            	<template v-slot:logo="e">
            		<div :title="\`\${leftHeadInfo.label} \\r\\n \`\${leftHeadInfo.text}\`" class="logo f-c a-i-c b-i2" style="height: 60px;">
            			<img class="w-40 h-40 b-t m4" :src="leftHeadInfo.image"></img>
            			<div v-if="e.item.collapse">
            				<div class="fs14 c-f fs-w-b c-f" style="text-shadow: 1px 1px #f3f3f373;color: #fefefe;">
            					{{leftHeadInfo.label}}
            				</div>
            				<div class="fs f-s a-i-c c-f m-t2">{{leftHeadInfo.text}}</div>
            			</div>
            		</div>
            	</template>
            </ld-frame>
            \`\`\`
            ### \`home.vu\`

            \`\`\`javascript
            //home.vue
            /**
             * tab
             * @param {Object} e
             */
            openTabs(e) {
            	this.$refs.frame.menuClick(e);
            },
            /**
             * tab
             * @param {Object} e
             */
            closeTabs(item) {
            	if (typeof item['showConfirm'] === 'boolean' && item['showConfirm'] == false) {
            		this.$refs.frame.closeTabPageByProp(item['prop']);
            		return;
            	}
            	this.$confirm(\`\${item['label']}\`, '', {
            		confirmButtonText: '',
            		cancelButtonText: '',
            		type: 'warning'
            	}).then(() => {
            		this.$refs.frame.closeTabPageByProp(item['prop']);
            	}).catch(() => {});
            	return;
            },
            /**
             * 
             * @param {Object} item
             */
            refreshTabs(item) {
            	this.$refs.frame.refreshTabByProp(item['prop']);
            }
            \`\`\`
            ###  \`ld-frame\`
            \`\`\`javascript
            //\`ld-frame\`
            refreshTabs() {
            	this.$emit("events", {
            		eventMethod: 'refreshTabs',
            		eventParam: {prop: 'page1'} //ld-frame layoutprop
            	});
            },
            openTabs(e) {
            	this.$emit('events', {
            		eventMethod: 'openTabs',
            		eventParam: {prop:'page1',label:'1',icon:'el-icon-pie-chart',page:'page1'} //prop
            	});
            },
            closeTabs(e) {
            	this.$emit("events", {
            		eventMethod: 'closeTabs',
            		eventParam: {showConfirm:true,prop:'page1'} //ld-frame layoutprop
            	});
            }
            \`\`\`

            `.replace(/^       /gm,''),

          }
        ]
      };
    }
  }
</script>

<style>
</style>



