export default {
  firstLayout: (that) => {
    return [{
      label: {
        label: '',
        children: [{
            prop: 'nickName',
            label: ''
          },
          {
            prop: 'phone',
            label: ''
          },
          {
            prop: 'sex',
            label: '',
            html: (val) => {
              return `<div class="${val==1?'c-d':'c-s'}">${val==1?'':''}</div>`
            }
          },
          {
            prop: 'userType',
            label: '',
            format: (val) => {
              return val == 1 ? '' : ''
            }
          },
          {
            prop: 'birthday',
            label: '',
            format: (val) => {
              return !val ? '' : that.$ld.util.getDate(0, new Date(val));
            }
          },
          {
            prop: 'createdTime',
            label: '',
            format: (val) => {
              return !val ? '' : that.$ld.util.getDate(0, new Date(val));
            }
          },
          {
            prop: 'updatedTime',
            label: '',
            format: (val) => {
              return !val ? '' : that.$ld.util.getDate(0, new Date(val));
            }
          },
        ]
      },
    }]
  },
  firstAutoLoadDataApi: {
    remotePath: '/test/getUserInfo',
    remoteParam: {},
    remoteMethodType: "get",
    remoteTimeout: null,
  },
  elPagination: {
    pageSizes: [5, 10, 15, 20]
  },
  normalLayout: [{
      prop: 'nickName',
      label: ''
    },
    {
      prop: 'phone',
      label: ''
    },
    {
      prop: 'sex',
      label: '',
      html: (val) => {
        return `<div class="${val==1?'c-d':'c-s'}">${val==1?'':''}</div>`
      }
    },
    {
      prop: 'userType',
      label: '',
      format: (val) => {
        return val == 1 ? '' : ''
      }
    },
  ],
  twoLayout: [ {
      label: {
        label: '',
        children: [{
            prop: 'nickName',
            label: ''
          },
          {
            prop: 'phone',
            label: ''
          },
          {
            prop: 'sex',
            label: '',
            html: (val) => {
              return `<div class="${val==1?'c-d':'c-s'}">${val==1?'':''}</div>`
            }
          },

        ]
      }
    },{
      label: {
        label: '',
        children: [{
          prop: 'userType',
          label: '',
          format: (val) => {
            return val == 1 ? '' : ''
          }
        }]
      }
    },

  ],
  moreLayout:[{
  	label: {
  		label: 'xxx',
  		children:[
  			{
          label:{
            label:'',
            children:[
              {prop: 'nickName',label: '' },
              {prop: 'phone',label: '' },
              {prop: 'sex',label: '',html:(val)=>{return `<div class="${val==1?'c-d':'c-s'}">${val==1?'':''}</div>`}},
            ]
          }},
        {
          label:{
            label:'',
            children:[
              {prop:'userType',label:'',format: (val) => { return val == 1 ? '' : '';}}
            ]
          }
        }
  		]
  	}
  }]
}



