<template>
  <!--  -->
  <div class="box-b p10 h ld-doc">
    <el-card class="doc w h">
      <ld-doc :doc="doc" class="w h">

        <template v-slot:first="e">
          <ld-doc :doc="doc1" :show-outline="false" class="w h bor-d" style="height: 300px;">
            <template v-slot:foot="e">
              <foot></foot>
            </template>
          </ld-doc>
        </template>


        <template v-slot:secound="e">

          <ld-forms class="m-t10 m-b10" :form="forms" :layout="formLayout" :is-row="true">
            <template v-slot:buttons="e">
              <div style="position: relative;">
                <el-button type="primary m-l10" @click="copy('#doc2',true)"></el-button>
              </div>
            </template>
          </ld-forms>
          <ld-doc :doc="doc2" :aligns="forms['aligns']" :skin="forms['skin']" :show-outline="forms['showOutline']"
            :doc-width="forms['docWidth']" class="w bor-d" style="height: 1000px;">
            <template v-slot:foot="e">
              <foot></foot>
            </template>
          </ld-doc>
        </template>

        <template v-slot:third="e">

          <ld-doc :doc="doc3" class="w bor-d" style="height: 1000px;">
            <template v-slot:foot="e">
              <foot></foot>
            </template>
          </ld-doc>
        </template>

        <template v-slot:outlines="e">
          <ld-doc :doc="doc1" class="w h">
             <template v-slot:outline="e">
               <!---->
               <div class="w-200 b-d1">
                e
               </div>
             </template>
          </ld-doc>
        </template>

        <template v-slot:foot="e">
          <foot></foot>
        </template>
      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  import js from '@/util/js-formatter.js'
  import res from './doc-res.js';
  export default {
    name: 'doc',
    data() {
      return {
        forms: {
          showOutline: true,
          skin: 'dark',
          aligns: 'left'
        },
        formLayout: res.layout,

        doc1: {
          h1: 'ld-doc',
          h2: 'TA',
          h3: '',
          slot: 'foot',
        },
        doc2: [{
            title: 'title ',
            tip: '',
            h1: '.',
          },
          {
            h1: 'h1',
            h2: 'h2',
            h3: 'h3',
            tip_d: '`tip_d`',
            tip_i: '`tip_i`',
            tip_w: '`tip_w`',
            tip_s: '`tip_s`',
            tip_p: '`tip_p`,`tip`',
            p: 'p'
          },
          {
            h1: '.',
            p: '`csharp`, `html`, `css`, `javascript`, `php`, `dart`, `bash`, `shell`, `sql`, `vue`',
            tip_d: '`codeLanguages`()`codeLanguages=["csharp", "html", "css", "javascript", "php", "dart", "bash", "shell", "sql", "vue"]`',
          }, {
            csharp: `
              public void GetData(){
                return null;
              }`.replace(/^        /gm, ''),
            html: `<div>html</div>`.replace(/^        /gm, ''),
            css: `
            .c-d{
              color:var(--dangercolor);
            }
            `.replace(/^        /gm, ''),
            javascript: `
            console.log('javascript');
            `.replace(/^        /gm, ''),
            php: `
            <?php

            echo "Hello World!" ;
            echo "Hello PHP!" ;

            ?>
            `.replace(/^        /gm, ''),
            bash: ` npm i `,
            shell: ` npm update `,
            sql: ` SELECT * FROM USERS`,

          },
          {
            h1: '.',
            javascript: ``,
            p: 'foot!',
            slot: 'foot',
          }
        ],
        doc3: [{
            md: '```javascript\n' +
              'console.log("hello world!");\n' +
              '```',
          },
          {
            md: '# 1\n## 2',
          },
          {
            md: '```vue\n'+
            '<ld-doc :doc="doc3" class="w bor-d" style="height: 1000px;">\n  <template v-slot:foot="e">\n   <foot></foot>\n  </template>\n</ld-doc>\n'+
            '```\n>\n![gitHub](https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png)'
          }
        ],

        doc: [{
            title: '`ld-doc`',
            h1: '.',
            tip: '',
            tip_d: '`markdown`markdownmarkdown`vue-meditor`',
            tipd: ' `h1`,`h2`,`h3`',
            bash: 'npm i vue-meditor',
          },
          {
            h1: '.',
            h2: '`h1,h2,h3slot`',
          }, {
            html: `
          <ld-doc :doc="doc1" :show-outline="false" class="w h b-p2">
            <!--   doc slot-->
            <!-- footslot:'foot'-->
            <template v-slot:foot="e">
              <foot></foot>
            </template>
          </ld-doc>
          `.replace(/^       /gm, ''),
            javascript: `
            export default {
              data(){
                return {
                  data1:{
                    h1:'ld-doc',
                    h2:'TA',
                    h3:'',
                    slot:'foot',
                  }
                }
              }
            }
          `.replace(/^       /gm, ''),
            h2: '',
            slot: 'first',
          },
          {
            h1: '.`doc`',
            h2: '`Array`,`Object`,`String`',
            h3: '',
            tip_d: 'TAObject,',
            slot: 'secound',
          },
          {
            h1: '.`markdown``md`',
            h2: '`markdown``md`md',
            tip_d: '`vue-meditor``npm i vue-meditor`markdown`vue-meditor`',
            slot: 'third',
            p:'mdmarkdown',
            html:`
            <ld-doc :doc="doc" class="w bor-d" style="height: 1000px;">
              <template v-slot:foot="e">
                <foot></foot>
              </template>
            </ld-doc>
            `.replace(/^          /gm,""),
            javascript:`
            export default {
              data(){
                return {
                  doc:[{
                        md: '\`\`\`javascript\\n'+
                            '  console.log("hello world!");\\n'+
                            '\`\`\`',
                      },
                      {
                        md: '# 1\\n## 2',
                      },
                      {
                        md: '\`\`\`vue\\n'+
                            '<ld-doc :doc="doc3" class="w bor-d" style="height: 1000px;">\\n  <template v-slot:foot="e">\\n   <foot></foot>\\n  </template>\\n</ld-doc>\\n'+
                            '\`\`\`\\n>\\n![gitHub](https://github.githubassets.com/images/modules/logos_page/GitHub-Mark.png)'
                      }
                    ],
                  }
                }
            }`.replace(/^          /gm,"")
          },
          {
            h1: '.`slot`',
            h2: '`slot`()',
            tip_d: '`first`,',
            javascript:`
            export default {
              data(){
                return {
                  doc:[
                    {slot:'first'}
                  ]
                }
              }
            }
            `.replace(/^          /gm,""),
            tipd:'TA',
            html:`
             <ld-doc :doc="doc" class="w h">
                <!--first -->
                <template v-slot:first="e">
                  <!---->
                </template>
             </ld-doc>
            `.replace(/^          /gm,""),
          },
          {
            h1:'.',
            h2:'',
            md:`||||||
            |-|-|-|-|-|
            |doc|Array,Object,String||||
            |code-languages|Array||\`["csharp", "html", "css", "javascript", "php", "dart", "bash", "shell", "sql", 'vue']\`||
            |show-outline|Boolean||true||
            |skin|String|||\`light\`\\|\`dark\`;|
            |aligns|String||left|\`left\`\\|\`right\`\\|\`center\`|
            |doc-width|String||100%|;100px100%|
            |MdAnchorLinkTarget|String||_self|md_blank,_self,_parent,_top,framename|
            `
          },
          {
            h2:'doc',
            md:`|||
                |-|-|
                |p||
                |h1||
                |h2||
                |h3||
                |slot|()|
                |tip|tip\\|tip_p tip_d tip_w  tip_i  tip_s (\`tip\`\`p\` \`d\` \`w\` \`i\` \`s\`,tip;\`tip_d\`<=>\`tip__d\`<=>\`tipd\`)|
                ||\`csharp\`, \`html\`, \`css\`, \`javascript\`, \`php\`, \`dart\`, \`bash\`, \`shell\`, \`sql\`, \`vue\`|
                |md\|markdown\\|markdown|`
          },
          {
            h2:'',
            md:`||||
            |-|-|-|
            ||||`
          },
          {
            h1:'.`outline`',
            tip_d:'`outline`',
            slot:'outlines',
            html:`
             <ld-doc :doc="doc1" class="w h">
                <template v-slot:outline="e">
                  <!---->
                  <div class="w-200 b-d1">
                    e
                  </div>
                </template>
             </ld-doc>
            `.replace(/^          /gm,""),
          },
          {
            slot: 'foot'
          }
        ]
      }
    },
    methods: {
      copy(selector, isHTML) {
        const flg = this.$ld.util.copyToClipboard(JSON.stringify(this.forms));
        this.$message[flg ? 'success' : 'error'](flg ? '' : '');
      }
    },
    created() {
      this.doc2[this.doc2.length - 1]['javascript'] =
        js.format(JSON.stringify(this.doc2)).replace(/^/, '\n')
    }
  }
</script>

<style>
  .loading .el-card__body {
    padding: 0 !important;
  }
  .ld-doc-item .m-r10.over-a-y.h.p10.box-b.f-n-c-w,.ld-doc-item .over-a-y.h.p10.box-b.f-n-c-w{
    width: 300px!important;
    max-width: 300px!important;
  }
</style>



