<template>
  <div class="box-b p10 h ld-doc">
    <el-card class="doc w h">
      <!--  -->
      <ld-doc :doc="doc" class="w h">

      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        doc:[
          {
            title:'',
            tip:'...<a target="_black"  href="https://github.com/dongmingzhixiu/layout-dynamic-ui/blob/main/doc/ld-util.md">gitHubld-util</a>',
            md:`# **\`ld-util\`**
            # 
            >   

            ## 1.[\`changeImagePath\` ]("#changeImagePath")
            ## 2.[\`uuid\`](#uuid) 
            ## 3.[\`getDateByNumber\`](#getDateByNumber) 
            ## 4.[\`getYearDiff\`](#getYearDiff) 
            ## 4.[\`getMonthDiff\`](#getMonthDiff) 
            ## 4.[\`getDayDiff\`](#getDayDiff) 
            ## 5.[\`getDate\`](#getDate) 
            ## 6.[\`getTime\`](#getTime) 
            ## 7.[\`getDateTime\`](#getDateTime) 
            ## 8.[\`getTimeSplit\`](#getTimeSplit) 
            ## 9.[\`pingyin\`](#pingyin)  
            <!-- ## 9.[\`encryption\`](#encryption)  -->
            ## 10.[\`randomNum\`](#randomNum)  ()
            ## 11.[\`randomNumFormula\`](#randomNumFormula) 
            ## 12.[\`randomChar\`](#randomChar) [0-9a-z]
            ## 13.[\`bigHumpToSmall\`](#bigHumpToSmall)  getName=>get_name
            ## 14.[\`smallHumpToBig\`](#smallHumpToBig)  get_name=>getName
            ## 15.[\`firstCharCmallHumpToBig\`](#firstCharCmallHumpToBig)  get_name=>GetName
            ## 16.[\`objConverArray\`](#objConverArray)  {a:1,b:2,c:3}=>[{"$key":"a","$value":1},{"$key":"b","$value":2},{"$key":"c","$value":3}]
            ## 17.[\`hashCode\`](#hashCode) hashCode
            ## 18.[\`getDataType\`](#getDataType) int float string object array
            ## 19.[\`objToParam\`](#objToParam) url{a:1,b:2,c:3} => a=1&b=2&c=3&  ?a=1&b=2&c=3&
            ## 20.[\`urlToObj\`](#urlToObj)  url  a=1&b=2&c=3&  ?a=1&b=2&c=3& =>{a:1,b:2,c:3}
            ## 21.[\`digitUppercase\`](#digitUppercase)   123123213.34 =>
            ## 22.[\`insertToStr\`](#insertToStr)  insertToStr("123456",2,"a")  => 12a3456
            ## 23.[\`insertAndReplaceToStr\`](#insertAndReplaceToStr)  insertAndReplaceToStr("12345",2,4,"ab") => 12ab5
            ## 25.[\`idCardCheck\`](#idCardCheck)  
            ## 26.[\`getIp\`](#getIp)  ip
            ## 27.[\`cookie\`](#cookie) cookie
            ## 28.[\`copyToClipboard\`](#copyToClipboard)
            # 

            ## changeImagePath

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |url|String|||
            |serverPath|String|||
            |defaultPath|String|||

            ### 
            \`\`\`javascript

            this.$ld.util.changeImagePath ("fileUpload/1/2/3/4.png","http://127.0.0.1:8080/test"); //"http://127.0.0.1:8080/test/fileUpload/1/2/3/4.png"

            this.$ld.util.changeImagePath ("","http://127.0.0.1:8080/test","http://127.0.0.1:8080/test/default/default.png");//"http://127.0.0.1:8080/test/default/default.png"

            \`\`\`

            ## uuid

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |len|Number||uuid;2-36)|
            |binary|Number||1628;2-36)|

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.uuid(); //81869db0-cdb6-48a2-83dd-f9c7d7132aa3

            //2.
            this.$ld.util.uuid(32);//89dc93f8-f242-447f-9348-85f40f3a

            //3.
            this.$ld.util.uuid(32,34);//segpemtu-jk11-49r2-atkd-ui0v87pf

            \`\`\`

            ## getDateByNumber

             > 


            ### 

            |||||
            |:---|:---|:---|:---|
            |date|Date|||

            ### 
            \`\`\`javascript

            //1. 1593317580211
            this.$ld.util.getDateByNumber ('1593317580211');//"2020-06-28 12:13:00"
            \`\`\`

            ## getYearDiff

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |endDate|String\Date|||
            |startDate|String\Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getYearDiff('2020-6-28 11:43:08','2020-5-28 11:43:20'); //0

            //2. 
            this.$ld.util.getYearDiff('2021-6-28 11:43:08','2020-7-28 11:43:20');//1

            \`\`\`

            ## getMonthDiff

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |endDate|String\Date|||
            |startDate|String\Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getMonthDiff('2020-6-28 11:43:08','2020-5-28 11:43:20'); //1

            //2. 
            this.$ld.util.getMonthDiff('2021-6-28 11:43:08','2020-7-28 11:43:20');//11

            \`\`\`

            ## getDayDiff

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |endDate|String\Date|||
            |startDate|String\Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getDayDiff('2020-6-28 11:43:08','2020-5-28 11:43:20'); //30

            //2. 
            this.$ld.util.getDayDiff('2020-6-28 11:43:08','2020-7-28 11:43:20');//-30

            \`\`\`


            ## getDate

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |addDay|Number|||
            |date|Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getDate(); //"2020-06-28"

            //2. +3
            this.$ld.util.getDate(3);//"2020-07-01"

            //3. -10
            this.$ld.util.getDate(-10);//"2020-06-18"

            //4.
            this.$ld.util.getDate(3,new Date('2020-6-30'));//"2020-07-03"

            \`\`\`

            ## getTime

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |addMillisecond|Number||1 = \`1*60*1000\`|
            |date|Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getTime(); //"11:31:59"

            //2. +30
            this.$ld.util.getTime(30*60*1000);//"11:36:57"

            //3. -1
            this.$ld.util.getTime(-1*60*60*1000);//"10:37:47"

            //4.
            this.$ld.util.getTime(1*60*1000,new Date('2020-6-30 11:20:22'));//"11:21:22"

            \`\`\`


            <!-- ## \`main.js\`
            \`\`\`javascript
                /**
                 *
                 import Vue from 'vue'
                   import App from './App'
                
                 */
                import orangeUtil from './utl/orange-util.js'; //orange-util.js 
                Vue.use(orangeUtil);
            \`\`\`
            *  -->
            --------------------------------------------------
            ## getDateTime

             > 

            ### 

            |||||
            |:---|:---|:---|:---|
            |addDay|Number||1 = \`1*60*1000\`|
            |date|Date|||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.getDateTime(); //"2020-06-28 11:39:29"

            //2. +1
            this.$ld.util.getDateTime(1);//"2020-06-29 11:39:58"

            //3. -1
            this.$ld.util.getDateTime(-1) //"2020-06-27 11:41:10"

            //4.
            this.$ld.util.getDateTime(1,new Date('2020-6-30 11:20:22'));//"2020-07-01 11:20:22"

            \`\`\`
            ## getTimeSplit

             > 

            ### 
            |||||
            |:---|:---|:---|:---|
            |dateTime|String\Date||[yyyy-MM-dd HH:mm:ss] |

            ### 
            \`\`\`javascript

            //1. 
            this.$ld.util.getTimeSplit(); //""

            //2. 
            this.$ld.util.getTimeSplit('2020-4-28 17:43:08');//""

            \`\`\`
            ## pingyin

             > 

            ### 
            |||||
            |:---|:---|:---|:---|
            |chineseToPinYin|Function|str||
            |getFirstChar|Function|str||

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.pingyin.chineseToPinYin(""); //"shangwu"

            //2.
            this.$ld.util.pingyin.getFirstChar("");//"sw"

            \`\`\`

            ## randomNum

             >  ()

            ### 
            |||||
            |:---|:---|:---|:---|
            |min|Number|| ()|
            |max|Number|| ()|
            |type|Number||\`0\` ()	\`1\`	()	\`2\`	()	\`3\`	()|

            ### 
            \`\`\`javascript

            //1.
            this.$ld.util.randomNum(0,10);//"2"

            //2.type
            this.$ld.util.randomNum(6.01,60.10,1);//"39"
            \`\`\`

            ## randomNumFormula

             >  

            ### 
            
            ### 
            \`\`\`javascript
            //
            this.$ld.util.randomNumFormula();//"5+4"
            \`\`\`

            ## randomChar

             > [0-9a-z]

            ### 
            |||||
            |:---|:---|:---|:---|
            |len|Number|||

            ### 
            \`\`\`javascript
            //
            this.$ld.util.randomChar(10);//"3c0qh4ctcj"
            \`\`\`

            ## bigHumpToSmall

             >  getName=>get_name

            ### 
            |||||
            |:---|:---|:---|:---|
            |str|String|||

            ### 
            \`\`\`javascript
            this.$ld.util.bigHumpToSmall("getName");//get_name
            \`\`\`


            ## smallHumpToBig

             >  get_name=>getName

            ### 
            |||||
            |:---|:---|:---|:---|
            |str|String|||

            ### 
            \`\`\`javascript
            this.$ld.util.smallHumpToBig("get_name");//getName
            \`\`\`

            ## firstCharCmallHumpToBig

             >  get_name=>GetName

            ### 
            |||||
            |:---|:---|:---|:---|
            |str|String|||

            ### 
            \`\`\`javascript
            this.$ld.util.firstCharCmallHumpToBig("get_name");//GetName
            \`\`\`

            ## objConverArray

             >   {a:1,b:2,c:3}=>[{"$key":"a","$value":1},{"$key":"b","$value":2},{"$key":"c","$value":3}]

            ### 
            |||||
            |:---|:---|:---|:---|
            |obje|Object|||

            ### 
            \`\`\`javascript
            this.$ld.util.firstCharCmallHumpToBig("{a:1,b:2,c:3}");//[{"$key":"a","$value":1},{"$key":"b","$value":2},{"$key":"c","$value":3}]
            \`\`\`

            ## hashCode

             > hashCode

            ### 
            ||||
            |:---|:---|:---|
            |str|String||

            ### 
            \`\`\`javascript

            //1. 
            this.$ld.util.hashCode('https://www.jianshu.com/p/96ecaa2cc989');//582844592
            //2.
            this.$ld.util.hashCode([1,2,3,42,5]);//-528217203

            \`\`\`

            ## getDataType

             > int float string object array

            ### 
            ||||
            |:---|:---|:---|
            |any|any||

            ### 
            \`\`\`javascript
            this.$ld.util.getDataType(1);//int
            this.$ld.util.getDataType(1.1);//float
            this.$ld.util.getDataType("1");//string
            this.$ld.util.hashCode([1,2,3,42,5]);//array
            this.$ld.util.hashCode({a:1,b:2,c:3});//object

            \`\`\`

            ## objToParam

             > url{a:1,b:2,c:3} => a=1&b=2&c=3&  ?a=1&b=2&c=3&

            ### 
            ||||
            |:---|:---|:---|
            |obj|Object||
            |isStartChart|Boolean|?|

            ### 
            \`\`\`javascript
            this.$ld.util.objToParam({a:1,b:2,c:3});//a=1&b=2&c=3
            this.$ld.util.objToParam({a:1,b:2,c:3},true);//?a=1&b=2&c=3
            \`\`\`

            ## urlToObj

             >  url  a=1&b=2&c=3&  ?a=1&b=2&c=3& =>{a:1,b:2,c:3}

            ### 
            ||||
            |:---|:---|:---|
            |obj|Object||
            |isStartChart|Boolean|?|

            ### 
            \`\`\`javascript
            this.$ld.util.urlToObj("http://127.0.0.1:8080/test/getInfo?a=1&b=2&c=3");//{a: "1", b: "2", c: "3", action: "http://127.0.0.1:8080/test/getInfo"}
            this.$ld.util.urlToObj("a=1&b=2&c=3");//{a: "1", b: "2", c: "3"}
            this.$ld.util.urlToObj("?a=1&b=2&c=3");//{a: "1", b: "2", c: "3"}
            \`\`\`

            ## digitUppercase

             >  123123213.34 =>

            ### 
            ||||
            |:---|:---|:---|
            |money|Number||

            ### 
            \`\`\`javascript
            this.$ld.util.digitUppercase(" 123123213.34");//
            \`\`\`

            ## insertToStr

             >  insertToStr("123456",2,"a")  => 12a3456

            ### 
            ||||
            |:---|:---|:---|
            |oldStr|String||
            |index|Number||
            |insertStr|String||

            ### 
            \`\`\`javascript
            this.$ld.util.insertToStr("123456",2,"a");// 12a3456
            \`\`\`
            ## insertAndReplaceToStr

             >  insertAndReplaceToStr("12345",2,4,"ab") => 12ab5

            ### 
            ||||
            |:---|:---|:---|
            |oldStr|String||
            |start|Number||
            |end|Number||
            |insertStr|String||

            ### 
            \`\`\`javascript
            this.$ld.util.insertAndReplaceToStr("12345",2,4,"ab");//12ab5
            \`\`\`
            ## idCardCheck

             > 

            ### 
            ||||
            |:---|:---|:---|
            |cardNo|String||

            ### 
            \`\`\`javascript
            this.$ld.util.idCardCheck("622222222222222222");//false
            \`\`\`
            ## getIp

             >  ip

            ### 
            

            ### 
            \`\`\`javascript
            this.$ld.util.getIp();//127.0.0.1
            \`\`\`

            ## cookie
            >cookie 
            ## 
            \`\`\`
            this.$ld.util.cookie.get(key);
            this.$ld.util.cookie.set(key,value,day);
            \`\`\`

            ## copyToClipboard
            >copyToClipboard 
            > true,false;

            ### 
            ||||
            |:---|:---|:---|
            |selector|String|\`\`\`\`; ES6document.querySelector  ||
            |innerHTML|Boolean|innerHTML,trueinnerHTML,false:innerText selector|

            ## 
            \`\`\`
            //
            this.$ld.util.copyToClipboard("");

            // <p id="test">innerText
            this.$ld.util.copyToClipboard("#test");
            this.$ld.util.copyToClipboard("#test",false);

            // <p id="test">innerHTML
            this.$ld.util.copyToClipboard("#test",true);
            \`\`\`

            `.replace(/^       /gm,''),
          }
        ]
      };
    }
  }
</script>

