export default {
  layout: [{
      prop: 'skin',
      label: '',
      type: 'select',
      options: [{
        label: '',
        value: 'dark'
      }, {
        label: '',
        value: 'light'
      }]
    },
    {
      prop: 'background',
      label: '',
      type: 'color'
    },
    {
      prop: 'color',
      label: '',
      type: 'color'
    },
    {
      prop: 'loadingText',
      label: '',
      type: 'text',
      value: ''
    }
  ]
}



