<template>
  <!--  -->
  <div class="box-b p10 h ld-doc">
    <el-card class="doc w h">
      <ld-doc :doc="doc" class="w h" doc-width="100%">
        <template v-slot:first="e">
          <ld-page-tabs :tabs="tabs" class="w" style="height: 400px;"></ld-page-tabs>
        </template>

        <template v-slot:second="e">
          <ld-forms class="m-t10 m-b10" :form="forms" :layout="formLayout" :is-row="true">
            <template v-slot:buttons="e">
              <div style="position: relative;">
                <el-button type="primary m-l10" @click="copy()"></el-button>
                <div id="customCode" class="o0" style="position: absolute;">
                  {{forms}}
                </div>
              </div>
            </template>
          </ld-forms>
          <ld-page-tabs :show-close="forms['showClose']" :show-refresh="forms['showRefresh']"
            :show-confirm="forms['showConfirm']" :tabs="tabs" class="w" style="height: 400px;"></ld-page-tabs>
        </template>

        <template v-slot:foot="e">
          <foot></foot>
        </template>
      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  import res from './page-tabs-res.js'
  export default {
    name: 'pageTabs',
    data() {
      return {
        forms: {},
        formLayout: res.layout,

        tabs: [{
            icon: 'el-icon-c-scale-to-original',
            prop: 'loading',
            label: 'loadingPage',
            page: 'loading',
            showClose: false
          },
          {
            prop: 'index',
            label: '',
            page: 'install'
          },
          {
            prop: 'baidu',
            label: '',
            page: 'https://www.baidu.com',
            showRefresh: false
          }
        ],


        doc: [{
            title: '`ld-page-tabs`',
            h1: '.',
            tip: '`tabs`(`showClose`)(`showRefresh`)`',
            slot: 'first',
            html: `
          <ld-page-tabs :tabs="tabs" class="w" style="height: 400px;"></ld-page-tabs>
          `.replace(/^        /gm, ''),
            javascript: `
          export default {
            data() {
              return {
                tabs: [
                  //
                  {icon: 'el-icon-c-scale-to-original',prop: 'loading',label: 'loadingPage',page: 'loading',showClose: false},
                  {prop: 'index',label: '',page: 'install'},
                  // 
                  {prop: 'baidu',label: '',page: 'https://www.baidu.com',showRefresh: false}
                ]
              }
            }
          }`.replace(/^        /gm, ''),
          },
          {
            h1: '.',
            tip_d: '`ld-page-tabs``showClose``showRefresh`TA`tabs``showClose``showRefresh``ld-page-tabs``tabs``showClose``showRefresh``tabs`',
            slot: 'second',
          },
          {
            h1: '.page',
            tip_d: '`page````vue`',
          },
          {
            h2: '',
            javascript: `{prop:'baidu',label:'','https://www.baidu.com'}`,
          },
          {
            h2: 'vue',
            tip: 'vue',
            h3: '',
            javascript: `
            //xxx.js
            import loading from '@/pages/loading/loading'
            const install = (Vue, opts = {}) => {
              //
            	Vue.component('loading', loading);
              //..... 
            }
            //install
            export default {
            	install
            }


            //main.js
            import pageImport from './xxxx.js';
            Vue.use(pageImport);
            `.replace(/^        /gm, ''),
          }, {
            javascript: `{prop: 'loading',label: 'loadingPage',page: 'loading'},`,
            slot: 'second',
          },
          {
            h1: '.',
            h2: '`ld-page-tabs` ',
            md: `|||||||
                |-|-|-|-|-|-|
                |tabs|Array[Object]||||tab,|
                |showClose|Boolean||true|||
                |showRefresh|Boolean||true|||
                |selected|String\\|Number|tab|||tabNumber,tabprop|
                |showConfirm|Boolean||true|||
                |closeBefore|Function(item)|true,false||||
                |refreshTabBefore|Function(item)|true,false||||
                |passEventUp|Boolean||true|||
`
          },
          {
            h2: ' `tabs`',
            tip_i: 'ld-page-tabs tabs ',
            md: `|||||||
            |-|-|-|-|-|-|
            |prop|String|||||
            |label|String|||||
            |page|String|(vue)\\|(http://https://)||||
            |showRefresh|Boolean|||||
            |showClose|Boolean|||||
            |icon|String||||element-uiicon|
            `
          },
          {
            h2: 'Event ',
            md: `||||||
            |-|-|-|-|-|
            |click|Function(item)||||
            |close|Function(item)||||
            |refresh|Function(item)||||
            `
          },
          {
            h2: 'Slot ',
            md: `|||||
            |-|-|-|-|
            |page||item {} tab||
            `
          },
          {
            h2: '`passEventUp`',
            tip_d: ''
          },
          {
            tip_i: '',
            javascript: `
            	//
            	this.$emit('events',{eventMethod:'click',eventParam:{}});
            `.replace(/^            /gm, ''),
          },
          {
            h3: '1.`passEventUp=true` ',
            tip_i: '`ld-page-tabs``@events`(`events`),',
            html: `
            <ld-page-tabs @events="getEvent"></ld-page-tabs>
            `.replace(/^            /gm, ''),
          },
          {
            h3: '2.`passEventUp=false` ',
            tip_i: '`ld-page-tabs``@click`(`click``eventMethod`),',
            html: `
           <ld-page-tabs @click="getEvent"></ld-page-tabs>
           `.replace(/^          /gm, ''),
          },
          {
            tip_d: '`ld-page-tabs``@events``@click`,`passEventUp=true``eventMethod`',
            javascript: `
            export default {
              methods:{
                getEvent(event){
                  // 
                }
              }
            }`.replace(/^           /gm, ''),
          },


          {
            slot:'foot'
          }
        ]
      }
    },
    methods: {
      copy() {
        let flg = this.$ld.util.copyToClipboard(JSON.stringify(this.forms));
        this.$message[flg ? 'success' : 'error'](flg ? "" : "Ctrl+C,Ctrl+V");
      }
    }
  }
</script>

<style>
  .m-r10.box-shadow.over-a-y.h.p10.box-b {
    max-width: 300px !important;
    min-width: 300px !important;
  }
</style>



