<template>
  <div class="box-b p10 h ld-doc">
    <el-card class="doc w h">
      <!--  -->
      <ld-doc :doc="doc" class="w h">

      </ld-doc>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: '',
    data() {
      return {
        doc: [{
          title: 'Axios',
          tip: '...<a target="_black"  href="https://github.com/dongmingzhixiu/layout-dynamic-ui/blob/main/doc/axios.md">gitHubaxios</a>',
          md:`
          # Axios 
          ## 1.

          - 

          \`\`\`javascript
          //vue
          this.$ld.requestSetting.serverPath.set('http://127.0.0.1:18085/frame/');
          \`\`\`
          > vue

          ## 2.axios
          - axios\`this.$ld.requestSetting.init\`

          \`\`\`javascript
           this.$ld.requestSetting.init= (axios) => {
              alert('axios');
              return axios;
            }
          \`\`\`
          > \`axios\`axios\`axios\`

          ## 3.
          - \`this.$ld.requestSetting.intercept\`

          - - 

          \`\`\`javascript
           //
          this.$ld.requestSetting.interceptor = {
            /**
             * 
             */
            before: (event) => {
              // true false ,
              return true;
            },
            /**
             * 
             */
            after: (event) => {
               //
               return event.data;
            }
          }

          \`\`\`
          - - Promise

          >  Promise 

          \`\`\`javascript
           //
          this.$ld.requestSetting.interceptor = {
            /**
             * 
             */
            before: (event) => {
             /**
              *  Promise.resolve  truefalse
              *  Promise.resolve(true);  //
              *      Promise.resolve(false); //
              */

              return this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
              }).then(() => {
                // true false ,
                return Promise.resolve(true);
              }).catch(() => {
                this.$message.info("");
                // true false ,
                return Promise.resolve(false);
              });
            },
            /**
             * 
             */
            after: (event) => {
              /**
               *  Promise.resolve 
               *  Promise.resolve(event);  //
               */
              return Promise.resolve(event);
            },
            /**
             * 
             */
            timeout:(err)=>{
            	return err;
            }
          }

          \`\`\`

          ## 4.\`Promise\`

          - \`this.$ld.request\`

          - -  

          \`\`\`javascript
           this.$ld.request('test/getUserInfo', 'get',{}).then(res => {
              //
            }, interceptError => {
              //
              console.log(interceptError);
            })
          \`\`\`

          - \`request\`

          ||||||||
          |-|-|-|-|-|-|-|
          |1|router|String||-||'test/getUserInfo'|
          |2|methodType|String||get|get\`\|\`post|'get'|
          |3|data|Object\`\|\`Array||{}||\`{userName:'188888888',password:'1111111'}\`|
          |4|timeout|Number||60000|();\`this.$ld.requestSetting.config.timeout = 2000;\`|1000*2|


           > (get;post)

           - - get 


           \`\`\`javascript
            this.$ld.getRequest('test/getUserInfo',{}).then(res => {
               //
             }, interceptError => {
               //
               console.log(interceptError);
             })
           \`\`\`

           - - post 

          \`\`\`javascript
           this.$ld.postRequest('test/getUserInfo',{}).then(res => {
              //
            }, interceptError => {
              //
              console.log(interceptError);
            })
          \`\`\`

          - \`getRequest\`\`postRequest\`

          ||||||||
          |-|-|-|-|-|-|-|
          |1|router|String||-||'test/getUserInfo'|
          |2|data|Object\`\|\`Array||{}||\`{userName:'188888888',password:'1111111'}\`|
          |3|timeout|Number||60000|();\`this.$ld.requestSetting.config.timeout = 2000;\`|1000*2|

          ## 5.
          > sessionIdsessionId

          \`\`\`javascript
           this.$ld.requestSetting.init= (axios) => {
              axios.defaults.withCredentials = true;
              return axios;
            }
          \`\`\`
          > mockjs
          \`\`\`javascript
          this.$ld.requestSetting.config.isMock=true;
          \`\`\`


          `.replace(/^     /gm,'')
        }]
      };
    }
  }
</script>

<style>
</style>



