                     const start = new Date();
                     start.setFullYear(start.getFullYear() - 1);
                     picker.$emit('pick', start);
                   }
                 }, {
                   text: '',
                   onClick(picker) {
                     const date = new Date();
                     picker.$emit('pick', date);
                   }
                 }, {
                   text: '',
                   onClick(picker) {
                     const start = new Date();
                     start.setMonth(start.getMonth() - 6);
                     picker.$emit('pick', start);
                   }
                 }]
               },
             }
            ]`.replace(/^          /gm, ''),
          },
          {
            H3: '6.4',
            md: `||||||||
            |-|-|-|-|-|-|-|
            |align|String||left, center, right|left|||
            |dateType|String||year/month/date<br/>/dates/week/datetime<br/>/datetimerange/daterange<br/>/monthrange|date|||
            |pickerOptions|Object|||||[Element-UIpicker-options](https://element.eleme.cn/#/zh-CN/component/date-picker)|
            |startPlaceholder|String|<br/>|||datetimerange:,<br/>monthrange:,<br/>datetimerange:|\`dateType='datetimerange'\`\\|\`dateType='monthrange'\`\\|\`dateType='daterange'\`|
            |endPlaceholder|String|<br/>|||datetimerange:,<br/>monthrange:,<br/>datetimerange:|\`dateType='datetimerange'\`\\|\`dateType='monthrange'\`\\|\`dateType='daterange'\`|
            |rangeSeparator|String|<br/>||||\`dateType='datetimerange'\`\\|\`dateType='monthrange'\`\\|\`dateType='daterange'\`|
            `,
