            slot: 'switch',
            javascript: `
            layout:[
              {prop:'switch1',label:'1',type:'switch'},
              {prop:'switch2',label:'2',type:'switch',activeText:'',inactiveText:''},
            ]`.replace(/^         /gm, ''),
            H3: '13.2',
            md: `||	|||||
            |-|-|-|-|-|-|
            |activeText|switch |string|-|-||
            |inactiveText|switch |string|-|-|||`
          },
          {
            h2: '14.`type="slider"`',
            h3: '14.1',
            slot: 'slider',
            javascript: `
            layout:[
              {prop:'slider1',label:'1',type:'slider',showStops:false,value:30},
              {prop:'slider2',label:'2',type:'slider',showStops:true,value:50,formatTooltip:(val)=>{return val+'%'}},
              {prop:'slider2',label:'2',type:'slider',showStops:true,value:80,marks:{20:'20',50:'50',80:'80'}},
            ]`.replace(/^         /gm, ''),
            H3: '14.2',
            md: `||	||||
            |-|-|-|-|-|
            |max||Number||	1000|
            |min||Number||	0|
            |showStops||Boolean||true|
            |formatTooltip|	 tooltip message|function(value)|-|-|
            |marks| key  number  [min, max] [Element-UImarks](https://element.eleme.cn/#/zh-CN/component/slider)|object|-|-|`
          },
          {
            h2: '15.`type="rate"`',
            h3: '15.1',
            slot: 'rate',
            javascript: `
            layout:[
              {prop:'rate1',label:'1',type:'rate'},
              {prop:'rate2',label:'1',type:'rate',showText:true},
              {prop:'rate3',label:'1',type:'rate',showScore:true},
              {prop:'rate4',label:'1',type:'rate',showText:true,texts:['','','','','']},
              {prop:'rate5',label:'1',type:'rate',showText:true,colors:['green','blue','orange']}
            ]`.replace(/^         /gm, ''),
            H3: '15.2',
            md: `||	||||
            |-|-|-|-|-|
            |showText| texts |boolean||	false|
            |showScore|show-score  show-text |boolean||	false|
            |texts||array||['', '', '', '', '']|
            |colors|icon  3  3 |array/object||['#F7BA2A', '#F7BA2A', '#F7BA2A']|`
          },
          {
            h2: '16.`type="color"`',
            h3: '16.1',
            slot: 'color',
            javascript: `
          layout:[
            {prop:'color1',label:'1',type:'color',value:'red'},
            {prop:'color2',label:'2',type:'color',showAlpha:false,value:'blue'},
            {prop:'color3',label:'3',type:'color',predefine:['#ff4500','#ff8c00','#ffd700','#90ee90','#00ced1','#1e90ff','#c71585','rgba(255, 69, 0, 0.68)','rgb(255, 120, 0)','hsv(51, 100, 98)','hsva(120, 40, 94, 0.5)','hsl(181, 100%, 37%)','hsla(209, 100%, 56%, 0.73)','#c7158577']},
          ]`.replace(/^         /gm, ''),
            H3: '16.2',
            md: `||	||||
          |-|-|-|-|-|
          |showAlpha||boolean||true|
          |predefine||Array||['#ff4500','#ff8c00','#ffd700','#90ee90','#00ced1','#1e90ff','#c71585','rgba(255, 69, 0, 0.68)','rgb(255, 120, 0)','hsv(51, 100, 98)','hsva(120, 40, 94, 0.5)','hsl(181, 100%, 37%)','hsla(209, 100%, 56%, 0.73)','#c7158577']|
          `
          },
          {
            h2: '17.`type="transfer"`',
            h3: '17.1',
            slot: 'transfer',
            javascript: `
           layout:[
             {
               prop: 'transferTest', type: 'transfer',label: '',parseType: 'split',
               options: [
                 {label: '',value: '1'},
                 {label: '',value: '2'},
                 {label: '',value: '3'},
               ],
               splitChart: ','
             },
           ]`.replace(/^         /gm, ''),
            H3: '17.2',
            md: `||	||||
          |-|-|-|-|-|
          |title|,|String,Array||['','']'\|'|
          |options||Array|||
          |title|,|String,Array||['','']'\|'|
          |leftDefaultChecked| key |array||[]|
          |rightDefaultChecked| key |array||[]|
          `
          },
          {
            h2: '18.`type="param "`',
            h3: '18.1',
            slot: 'param',
            javascript: `
           layout:[
             {
               prop: 'likeys',
               type: 'param',
               label: '',
               dataType: 'array',
               parseType: 'json',
               value: JSON.stringify(["", "1"])
             },
             {
               prop: 'likeys2',
               type: 'param',
               label: '',
               dataType: 'object',
               parseType: 'json',
               value: JSON.stringify({a:1,b:2,c:''})
             },
           ]`.replace(/^         /gm, ''),
            H3: '18.2',
            md: `||	||||
          |-|-|-|-|-|
          |dataType|,\`dataType\`,\`dateType\`|String|object,array|-|
          `
          },
          {
            h2: '19.`type="datakey "`',
            tip_d: '',
            h3: '19.1',
            slot: 'datakey',
            javascript: `
           layout:[
             {prop: 'datakey1', type: 'datakey',label: '',value: 'EC62246E-876A-436C-AACF-4C8E57BB290EB'},
           ]`.replace(/^         /gm, ''),
          },
          {
            h2: '20.`type="tip "`',
            tip_d: 'fromform',
            h3: '20.1',
            slot: 'tip',
            javascript: `
           layout:[
             {prop:'tip',label:'',type:'tip',tip:\`<div style="color:red"></div>\`,tipClass:'tip-p b-p1'},
             {prop:'tip2',label:'',type:'text',tip:'',tipClass:'tip-p b-p1'}
           ]`.replace(/^         /gm, ''),
          },
          {
            h2: '21.`type="slot "`',
            tip_d: '`slot`',
            h3: '21.1',
            tip_i:'',
            slot: 'slots',
            javascript: `
            layout:[
              {prop:'customSlot', label:'',type:'slot'}
            ]`.replace(/^         /gm, ''),
            tipd:'`ld-forms`',
            html:`
            <ld-forms ref="slots" :form="{}" :layout="[{prop:'customSlot', label:'',type:'slot'}]">
              <!--  prop -->
              <template #customSlot="e">
                <div>{{e}} </div>
                 <!--  -->
              </template>
            </ld-forms>
            `.replace(/^         /gm, ''),
          },
          {
            h1:'.`layout`(`regex`)',
            tip:'`regex`',
            tip_d:'`regex`String,Regex,function',
            h2:'1.String:`regex`',
            tipd:'`regex`1.2. `"/abc/i"``"/abc/igms"``"/abc/"`',
            slot:'regexString',
            javascript:`
            layout:[
              //regex
              {prop:'stringRegex1',label:'',type:'text',regex:'',require:true,tip:'',tipClass:'tip-p b-p1'},
              //,  /  /   igmsRegExp,
              {prop:'stringRegex2',label:'',type:'text',regex:'/^[a-z].*[0-9]$/i',require:true,tip:'',tipClass:'tip-p b-p1'},
            ]`.replace(/^         /gm, ''),
          },{
            h2:'2.RegExp:`regex`',
            tipd:'',
            slot:'regexRegExp',
            javascript:`
            layout:[
              {prop:'regexRegExp',label:'',type:'text',regex:/^1[0-9]{10}$/,msg:'111'},
              {prop:'regexRegExp2',label:'',type:'text',password:true,regex:/^[^.'"\\/#!%]{6,12}$/,msg:'6-12'},
            ]`.replace(/^         /gm, ''),
          },
          {
            h2:'3.Function:`regex`',
            tipd:'truefalse',
            slot:'regexFunction',
            javascript:`
            layout:[
              {prop:'regexFunction',label:'',type:'text',regex:(val)=>{return val%2!=0},msg:''},
              {prop:'regexFunction2',label:'',type:'text',regex:(val)=>{return [1,3,5,7,9].includes(val)},msg:'1,3,5,7,9'},
            ]`.replace(/^         /gm, ''),
          },
          {
            h1:'.`layout``require`',
            tipd:'`require="true"`;`require="false"`',
            slot:'require',
            javascript:`
            layout:[
              //require
              {prop:'require',label:'',type:'text',require:false},
              {prop:'require2',label:'',type:'text',require:true},
            ]`.replace(/^         /gm, ''),
          },
          {
            h1:'.`layout``change`',
            tip_d:'`Function:change`',
          },
          {
            h2:'1.',
            slot:'changeProperty',
            javascript:`
            layout:[
              { prop: 'testRadio',type: 'radio',label: '',
                options: [
                  {label: '',value: '1'},
                  {label: '',value: '0'}
                ],
                //
                change:(val,event)=>{
                  return {
                        //prop:{}
                    addressName:{
                      visabled:val==1, // val==1prop='addressName'visabled()true()
                      //  visabled require regex msg type value 
                    },
                    sponsorship:{
                      visabled:val==0, // val==0prop='sponsorship'visabled()true()
                      //  visabled require regex msg type value 
                    },
                  }
                },
                value:'1'
              },
              //
              { prop: 'addressName',type: 'textarea',label: '',visabled:true,},
              //
              { prop: 'sponsorship',type: 'text',label: '',visabled:false,},
            ]`.replace(/^         /gm, ''),
          },
          {
            h2:'2.',
            slot:'changeForms',
            javascript:`
            layout:[
              { prop: 'testRadio',type: 'radio',label: '',
                options: [
                  {label: '',value: '1'},
                  {label: '',value: '0'}
                ],
                //
                change:(val,event)=>{
                  return {
                    /**
                     * form
                     * Function(value,event)    Object
                     *
                     */
                    //1 Object
                    form:{
                        //prop:value
                       textInfo:val==1 ? '123456' : 'abcde',
                       //
                       testRadio:val,
                     },
                     //2function(value,event)
                     form:(value,event)=>{
                       let form=event['form'];
                       form['textInfo']=val==1 ? '123456' : 'abcde';
                       return form;
                     }
                  }
                }
              },
              //
              {prop: 'textInfo',type: 'text',label: '',visabled:true},
            ]`.replace(/^         /gm, ''),
          },
          {
            h2:'3.',
            tip_d:'',
            slot:'changeLayout',
            javascript:`
            layout:[
              {prop: 'testRadio',type: 'radio',label: '',
                options: [
                  {label: '',value: '1'},
                  {label: '',value: '0'}
                ],
                //
                change:(val,event)=>{
                  return {
                    /**
                     * layout
                     * fromFunction(value,event)    Array
                     *
                     */
                    //1 Object
                    layout:[
                      //{}
                      //
                      {prop:'textInfo',type:'select',options:[{label:'1',value:'1'},{label:'2',value:'2'}],label:'select'}
                     ],

                    //2function(value,event)
                    layout: (value, event) => {
                      let layout = event['layout'];
                      let item = layout.filter(item => item.prop == 'textInfo');
                      if (item.length <= 0) {
                        return layout;
                      }
                      let index = layout.indexOf(item[0]);
                      item=item[0];
                      //textInfo
                      item['type'] = val != 1 ? 'text' : 'select';
                      item['label'] = val != 1 ? '': 'select';
                      item['readonly'] = false;
                      item['options'] = val != 1 ? [] : [{
                        label: '1',
                        value: '1'
                      }, {
                        label: '2',
                        value: '2'
                      }];
                      layout[index] = item;
                      return layout;
                    }
                  }
                }
              },
              {prop: 'textInfo',type: 'text',label: '',visabled:true},
            ]`.replace(/^         /gm, ''),
          },
          {
            h2:'4.',
            slot:'changeTest',
            javascript:`
            layout: [
              {prop: 'textInfo',type: 'text',tip:'',label: '',readonly: true,visabled: true,},
              {prop: 'select',tip:'',label: '',type: 'select',options:
                [
                  {label: '',value: '1'},
                  {label: '',value: '2'},
                  {label: '',value: '3'},
                  {label: '',value: '4'},
                  {label: '4-6',value: '5'},
                  {label: '4-6',value: '6'},
                  {label: '',value: '7'},
                ],
                change: (val, event) => {
                  return {
                    textInfo: {
                      label: val == '1' ? '' : '',
                      readonly: val == '2',
                      require: val == '3'||val=='7',
                      visabled: val != '4',
                      password:val=='7',
                      // 
                      regex: val=='6' ? /^[\u4e00-\u9fa5]{4,6}$/ : (textVal) => {
                        return function(textVal) {
                          if (val != "5"&&val!='7') {
                            return true;
                          }
                          return /^[0-9]{4,6}$/.test(textVal);
                        }
                      },
                      msg: val=='6' ? '' : '',
                    },
                  }
                }
              },
              {prop: 'changeType',label: '',type: 'select',tip:\`<span class='c-d p2'>form</span><span class='c-d p2'>layout</span>\`,
                options: [
                  {label: 'select',value: '1'},
                  {label: 'text',value: '2'}
                ],
                change: (val, event) => {
                  return {
                    /**
                     * form
                     * Function(value,event)    Object
                     *
                     */

                    //1 Object
                    // form: {
                    //   textInfo: ['7', '8', '9'].includes(val) ? '123456' : ''
                    // },
                    //2function(value,event)
                    form: (value, event) => {
                      let form = event['form'];
                      form['textInfo'] =val==2 ? '123456' : '';
                      return form;
                    },
                    /**
                     * layout
                     * fromFunction(value,event)    Array
                     *
                     */
                    //1 Object
                    // layout: [
                    //   {
                    //     prop: 'textInfo',
                    //     type: 'select',
                    //     options: [{
                    //       label: '1',
                    //       value: '1'
                    //     }, {
                    //       label: '2',
                    //       value: '2'
                    //     }],
                    //     label: 'select'
                    //   }
                    // ],

                    //2function(value,event)
                    layout: (value, event) => {
                      let layout = event['layout'];
                      let item = layout.filter(item => item.prop == 'textInfo');
                      if (item.length <= 0) {
                        return layout;
                      }
                      let index = layout.indexOf(item[0]);
                      item=item[0];
                      //textInfo
                      item['type'] = val != 1 ? 'text' : 'select';
                      item['label'] = val != 1 ? '': 'select';
                      item['readonly'] = false;
                      item['options'] = val != 1 ? [] : [{
                        label: '1',
                        value: '1'
                      }, {
                        label: '2',
                        value: '2'
                      }];
                      layout[index] = item;
                      return layout;
                    }
                  }
                }

              }
            ]`.replace(/^         /gm, ''),
          },
          {
            h1: ".`ld-forms`",
            tip:'',
          },
          {
            h2:'1.`saveFormsDataBefore` ',
            tipd:',1.2.<br/>`ld-forms``save-forms-data-before`',
            h3:'1.1',
            javascript:`
            this.$ld.saveFormsDataBefore=function(data){
            	data['createBy']="1";//
            	return data;
            }`.replace(/^         /gm, ''),
            H3:'1.2',
            html:`
            <ld-forms :save-forms-data-before="saveBefore" :clos="2" ref="changeForms" :form="{}" :layout="changeForms"> </ld-forms>
            `.replace(/^         /gm, ''),
          },
          {
            javascript:`
            export default{
              data(){
                return {
                  //
                  saveBefore:function(data){
                    data['updateTime']=new Date();//
                    return data;
                  }
                }
              }
            }
            `.replace(/^         /gm, ''),
          },
          {
            h2:'2.`saveFormsDataAfter` ',
            tipd:',1.2.<br/>`ld-forms``save-forms-data-after`',
            h3:'2.1',
            javascript:`
            this.$ld.saveFormsDataAfter=function(data){
            	if(result.code==0){
            			this.$message.success("");
            			return;
            		}
            		this.$message.error("");
            		return;
            }`.replace(/^         /gm, ''),
            H3:'2.2',
            html:`
            <ld-forms :save-forms-data-after="saveAfter" :clos="2" ref="changeForms" :form="{}" :layout="changeForms"> </ld-forms>
            `.replace(/^         /gm, ''),
          },
          {
            javascript:`
            export default{
              data(){
                return {
                  //
                  saveAfter:function(data){
                    if(result.code==0){
                        this.$message.success("");
                        return;
                      }
                      this.$message.error("");
                      return;
                  }
                }
              }
            }
            `.replace(/^         /gm, ''),
          },
          {
            slot: 'foot',

          }
        ]
      };
    },
    methods: {
      createTest1() {
        let res = this.$refs.form1.checkForm();
        if (res['error'] === true) {
          this.$message.error(res['msg']);
          return;
        }
        this.$message.success('');
      }

    },
    created() {
      this.$set(this.doc[1], 'javascript', res.testJavascript1);
    }
  }
</script>

