import layoutDynamicUi from 'layout-dynamic-ui'; //mockjs
const Mock = require('mockjs')
//  mock.Random 
const Random = Mock.Random;
//mockjs
Mock.mock('/api/select', (req, res) => { //postget/api/dataMock
  let list = [];
  for (let i = 0; i < 5; i++) {
    let listObject = {
      name: Random.cname(), //
      phone: Random.integer(10000000000, 19999999999),
      id: Random.integer(100, 9999), //
    }
    list.push(listObject);
  }
  return {
    data: list
  }
})
Mock.mock(new RegExp('/test/getUserInfo.*'), 'get', (req, res) => { //postget/api/dataMock.
  let request = layoutDynamicUi.util.urlToObj(req['url']);
  let list = [];
  let pageSize = parseInt(request['pageSize']) || 10;
  for (let i = 0; i < 10; i++) {
    let listObject = {
      nickName: Random.cname(), //
      phone: Random.integer(10000000000, 19999999999),
      id: (i+1)+"", //
      userType: Random.integer(0, 2),
      birthday: Random.date(),
      updatedTime: Random.date(),
      createdTime: Random.date(),
      address:Random.county(true),
      parentId:i<4?0:i>4?i-3:i>=9?9-4:1,
    }
    list.push(listObject);
  }
  return request['isPageHelper']+'' =='true'? {
    data: {
      list: list,
      currentPage: request['pageNum'],
      pageSize: pageSize,
      total: 100
    }
  } : {
    data: list
  }
})



