export default {
  layout: [{
      prop: 'skin',
      label: '',
      type: 'select',
      options: [{
        label: '',
        value: 'dark'
      }, {
        label: '',
        value: 'light'
      }],
      width:'160px'
    }, {
      prop: 'showOutline',
      label: '',
      type: 'select',
      options: [{
        label: '',
        value: true
      }, {
        label: '',
        value: false
      }],
      width:'200px'
    },
    {
      prop: 'aligns',
      label: '',
     type: 'select',
     options: [{
       label: '',
       value: 'left'
     }, {
       label: '',
       value: 'center'
     },
     {
       label: '',
       value: 'right'
     }],
     width:'160px'
    },
    {
      prop: 'docWidth',
      label: '',
      placeholder:'100%100px',
      type: 'text',
      value:'100%',
    },
  ]
}



